import i18next from "i18next";
import { useMe } from "../store";
import { LocaleConfig } from "react-native-calendars";
import moment from "moment";
import { useAsync } from "./index";
import { useEffect } from "react";

export const useLanguage = () => {
  const me = useMe();
  useEffect(() => {
    changeLanguage(me?.lang || "en").then();
  }, [me?.lang]);
  return me ? me.lang : "en";
};

let lngResources = {
  vi: {},
  en: {},
};

export const setupLanguage = async (language: string = "en") => {
  LocaleConfig.locales["vi"] = {
    today: "Hôm nay",
    amDesignator: "SA",
    pmDesignator: "CH",
    dayNames: [
      "Thứ 2",
      "Thứ 3",
      "Thứ 4",
      "Thứ 5",
      "Thứ 6",
      "Thứ 7",
      "Chủ nhật",
    ],
    dayNamesShort: ["CN", "T2", "T3", "T4", "T5", "T6", "T7"],
    monthNames: [
      "Tháng 1",
      "Tháng 2",
      "Tháng 3",
      "Tháng 4",
      "Tháng 5",
      "Tháng 6",
      "Tháng 7",
      "Tháng 8",
      "Tháng 9",
      "Tháng 10",
      "Tháng 11",
      "Tháng 12",
    ],
    monthNamesShort: [
      "T1",
      "T2",
      "T3",
      "T4",
      "T5",
      "T6",
      "T7",
      "T8",
      "T9",
      "T10",
      "T11",
      "T12",
      "T12",
    ],
  };

  LocaleConfig.defaultLocale = "en";

  await i18next.init({
    lng: language || "en",
    fallbackLng: "en",
    compatibilityJSON: "v3",
    debug: false,
    resources: {
      vi: {
        translation: {
          ...require("../assets/languages/vi.json"),
          ...(lngResources?.vi || {}),
        },
      },
      en: {
        translation: {
          ...require("../assets/languages/en.json"),
          ...(lngResources?.en || {}),
        },
      },
    },
  });
};

export const useSetupLanguage = () => {
  const currentLanguage = useLanguage();
  moment.locale(currentLanguage);
  return useAsync(() => {
    return setupLanguage(currentLanguage);
  }, [currentLanguage]);
};

export const changeLanguage = (lng: string) => {
  return i18next.changeLanguage(lng);
  //TODO: change moment locale
  //TODO: change LocaleConfig of react-native-calendars
};

export const addLangResources = async (resources: { [lng: string]: any }) => {
  for (let _lng in resources) {
    await i18next.addResourceBundle(
      _lng,
      "translation",
      {
        ...(resources?.[_lng] || {}),
      },
      true,
      true
    );
  }
};

export const translate = i18next.t.bind(i18next)<string>;

export { lngResources };
